<template>
  <div class="ViewOrderAdd">
    <v-dialog v-model="dialog" width="80%">
      <v-form ref="form">
        <v-card>
          <v-card-title class="d-flex justify-space-between error lighten-3 font-weight-bold">
            <div>床號:{{ user.bed_name }}</div>
            <div>姓名:{{ user.name }}</div>
            <div>日期:{{ date }}</div>
            <v-icon @click.stop="dialog = false">mdi-close</v-icon>
          </v-card-title>
          <v-divider></v-divider>
          <v-card-text class="mt-2">
            <v-text-field
              type="time"
              label="預約時間"
              v-model="list.time"
              :rules="emptyRules"
            ></v-text-field>
            <v-textarea
              label="預約單內容"
              v-model="list.content"
              :rules="emptyRules"
            ></v-textarea>
          </v-card-text>
          <v-divider></v-divider>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn class="primary" @click="orderOK" :loading="loading" :readonly="loading">確認預約</v-btn>
          </v-card-actions>
          <!-- {{ list }} -->
        </v-card>
      </v-form>
    </v-dialog>
  </div>
</template>

<script>
import dayjs from "dayjs";

export default {
  props: ["selectRoom","selectRoomData"],
  data() {
    return {
      dialog: false,
      emptyRules: [(v) => !!v || "不可空白"],
      list: {},
      user: {},
      orderData: "",
      date: "",
      loading:false,

    };
  },

  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("fromParent", function (data) {
      this.dialog = true;
      this.enterProcess(data);
    });
  },

  methods: {
    //打開dialog進入程序
    enterProcess(data) {
      console.log(data)
      this.user = data.user;
      this.date = data.date;
      this.orderData = data.orderData;

      this.list = {
        userSnkey : data.user.userSnkey,
        date : data.date,
        selectRoom: this.selectRoom,
        time : data.orderData.time,
        content : data.orderData.content,
        create_man: data.orderData.create_man,
        edit_man: data.orderData.edit_man ? data.orderData.edit_man : ''
      };
      this.$nextTick(() => {
        this.$refs.form.resetValidation();
      });
    },
    orderOK() {
      if (this.$refs.form.validate()) {
        this.loading = true;
        /*判斷是否同一天同一時段;
        2023.1.12 改為可在人員資料中的設定做切換;setvieworder為true時,表示不可
        */
        if (this.$store.state.cData.setvieworder){
          var nHour = this.list.time.split(":")[0];
          let match = this.selectRoomData.find(i=>{
            let iHour = i.time.split(":")[0]; 
            return ( nHour == iHour && i.date == this.list.date && i.userSnkey != this.list.userSnkey)
          })
          if(match){
            var pop = { msg: "同時段已被預約", type: true, theme: "warning" };
            this.$store.commit("snackbar", pop);
            this.loading = false;
            return false;
          }
        }
        //判斷是否同一天同一時段 end

        if(!this.orderData){
          this.list.create_man = this.$store.state.pData.username + "(" +  dayjs().format("YYYY-MM-DD HH:mm:ss") + ")"; //執行動作的人,時間,內容
          var url = "general/add/" + this.$store.state.databaseName + "/vieworder";
          var postData = {
            datalist: JSON.stringify(this.list),
          };
          var msg = "已新增";
        }else{
          if (!this.list.edit_man){this.list.edit_man = ""}
          this.list.edit_man = this.$store.state.pData.username + "(" + dayjs().format("YYYY-MM-DD HH:mm:ss") + ")" + this.list.edit_man; //執行動作的人,時間,內容
          var url = "general/edit/" + this.$store.state.databaseName + "/vieworder";
          var postData = {
            snkey:this.orderData.snkey,
            datalist: JSON.stringify(this.list),
            edit_man: "",
          };
          var msg = "已修改";
        }

        this.$api.options(url, postData)
        .then((rs) => {
          if (rs.state == 1) {
            var pop = { msg: msg, type: true, theme: "success" };
            this.$store.commit("snackbar", pop);
            this.$emit("getAllData");
            this.dialog = false;
            this.loading = false;
          }
        });
      }
    },
  },
};
</script>

