<template>
  <div class="vieworderlist">
    <ViewOrderAdd
      ref="editProcess"
      :selectRoom="selectRoom"
      :selectRoomData="selectRoomData"
      @getAllData="getAllData"
    />

    <v-card class="mt-2">
      <v-card-title>
        <span class="font-weight-bold">疫情訪視預約單</span>
        <v-spacer></v-spacer>
        <ViewOrderSearch :items="items" :userItems="userItems" @getAllData="getAllData" v-if="items.length && userItems.length" />
      </v-card-title>
      <v-card-text>
        <div class="d-flex align-center">
          <v-autocomplete
            label="病房 / 病歷號 / 姓名"
            :items="selectUserItems"
            v-model="selectRoom"
            return-object
          ></v-autocomplete>
          <v-spacer></v-spacer>
          <v-text-field
            type="date"
            label="日期"
            v-model="nowday"
          ></v-text-field>
          <div>
            <v-btn class="primary mx-3" @click="changeday(-7)">上周</v-btn>
            <v-btn class="primary" @click="changeday(7)">下周</v-btn>
          </div>
        </div>

        <div v-if="selectRoom">
          <v-simple-table class="form">
            <thead>
              <tr>
                <th rowspan="2">床號</th>
                <th rowspan="2">姓名</th>
                <th v-for="num in 7" :key="num">
                  {{ returnDay(num, "month") }}
                </th>
              </tr>
              <tr>
                <th class="red lighten-4">日</th>
                <th class="blue-grey lighten-5">一</th>
                <th class="blue-grey lighten-5">二</th>
                <th class="blue-grey lighten-5">三</th>
                <th class="blue-grey lighten-5">四</th>
                <th class="blue-grey lighten-5">五</th>
                <th class="red lighten-4">六</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="user in selectRoomFormData" :key="user.name">
                <td>{{ user.bed_name }}</td>
                <td>{{ user.name }}</td>
                <td @click.stop="addOrder(user, user.sun, 0)">
                  <span v-if="user.sun"
                    >{{ user.sun.time }}
                    <v-icon class="red--text" @click.stop="del(user.sun)"
                      >mdi-close</v-icon
                    >
                  </span>
                </td>
                <td @click.stop="addOrder(user, user.mon, 1)">
                  <span v-if="user.mon"
                    >{{ user.mon.time }}
                    <v-icon class="red--text" @click.stop="del(user.mon)"
                      >mdi-close</v-icon
                    ></span
                  >
                </td>
                <td @click.stop="addOrder(user, user.tue, 2)">
                  <span v-if="user.tue"
                    >{{ user.tue.time }}
                    <v-icon class="red--text" @click.stop="del(user.tue)"
                      >mdi-close</v-icon
                    ></span
                  >
                </td>
                <td @click.stop="addOrder(user, user.wed, 3)">
                  <span v-if="user.wed"
                    >{{ user.wed.time }}
                    <v-icon class="red--text" @click.stop="del(user.wed)"
                      >mdi-close</v-icon
                    ></span
                  >
                </td>
                <td @click.stop="addOrder(user, user.thu, 4)">
                  <span v-if="user.thu"
                    >{{ user.thu.time }}
                    <v-icon class="red--text" @click.stop="del(user.thu)"
                      >mdi-close</v-icon
                    ></span
                  >
                </td>
                <td @click.stop="addOrder(user, user.fri, 5)">
                  <span v-if="user.fri"
                    >{{ user.fri.time }}
                    <v-icon class="red--text" @click.stop="del(user.fri)"
                      >mdi-close</v-icon
                    ></span
                  >
                </td>
                <td @click.stop="addOrder(user, user.sat, 6)">
                  <span v-if="user.sat"
                    >{{ user.sat.time }}
                    <v-icon class="red--text" @click.stop="del(user.sat)"
                      >mdi-close</v-icon
                    ></span
                  >
                </td>
              </tr>
            </tbody>
          </v-simple-table>
        </div>

      </v-card-text>
<!-- {{selectRoom}} -->
    </v-card>

    <!-- 警告提示 -->
    <v-dialog v-model="viewDialog" persistent scrollable width="500">
      <v-card>
        <v-toolbar dark color="warning">
          <v-btn icon dark @click.stop="viewDialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title class="font-weight-black">訪視限制提示</v-toolbar-title>
        </v-toolbar>
        
        <v-card-text class="mt-2">
          <h3>{{ this.selectRoom.visitLimitContent }}</h3>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import dayjs from "dayjs";
import weekday from "dayjs/plugin/weekday";
import isBetween from "dayjs/plugin/isBetween";
dayjs.extend(weekday);
dayjs.extend(isBetween);
import ViewOrderAdd from "@/views/main/ViewOrderAdd";
import ViewOrderSearch from "@/views/main/ViewOrderSearch";

export default {
  components: { ViewOrderAdd, ViewOrderSearch },
  data() {
    return {
      items: [],
      userItems: [], //所有病歷資料
      bedItems: [], //所有病床資料
      selectRoom: "",
      nowday: dayjs().format("YYYY-MM-DD"),

      viewDialog:false,
    };
  },

  watch:{
    selectRoom(val){
      if (val.visitLimit == 'true'){
        this.viewDialog = true
      }else{
        this.viewDialog = false
      }
    }
  },

  mounted() {
    this.getItems("user/getAll/" + this.$store.state.databaseName, "user"); //取得病歷資料
    this.getItems(
      "bed/getAll/" + this.$store.state.databaseName + "/bed",
      "bed"
    ); //取得病床資料
    this.getAllData();
  },

  computed: {
    //回傳選擇病歷/人名/病房的選單
    selectUserItems() {
      return this.userItems.filter(i=>!!i.bed_name)
      .sort((a,b)=>{
        return a.bed_name > b.bed_name ? 1 : -1
      })
      .map((i) => {
        return {
          text: `${i.bed_name} / ${i.p_code} / ${i.name}`,
          bed_name: i.bed_name,
          visitLimit: i.visitLimit,
          visitLimitContent: i.visitLimitContent
        };
      });
      
    },
    //建構符合資料的表格內容
    selectRoomFormData() {
      return this.selectRoomUsers.map((user) => {
        return {
          userSnkey: user.snkey,
          bed_name: user.bed_name,
          name: user.name,
          sun: this.matchData(user.snkey, this.sunday),
          mon: this.matchData(
            user.snkey,
            dayjs(this.sunday).add(1, "day").format("YYYY-MM-DD")
          ),
          tue: this.matchData(
            user.snkey,
            dayjs(this.sunday).add(2, "day").format("YYYY-MM-DD")
          ),
          wed: this.matchData(
            user.snkey,
            dayjs(this.sunday).add(3, "day").format("YYYY-MM-DD")
          ),
          thu: this.matchData(
            user.snkey,
            dayjs(this.sunday).add(4, "day").format("YYYY-MM-DD")
          ),
          fri: this.matchData(
            user.snkey,
            dayjs(this.sunday).add(5, "day").format("YYYY-MM-DD")
          ),
          sat: this.matchData(user.snkey, this.saturday),
        };
      });
    },
    //回傳符合選擇的房間內的目前資料,條件:房名,時間也要剛好在指定範圍內
    selectRoomData() {
      if (this.selectRoom){
        return this.items.filter((i) => {
          return (
            JSON.stringify(i.selectRoom.bed_name).includes(this.selectRoom.bed_name.split('-')[0])
            &&
            dayjs(i.date).isBetween(this.sunday, dayjs(this.saturday), null, "[]")
          );
        });
      }
    },
    //取得病房名
    getRoom() {
      var roomArr = this.bedItems.map((i) => {
        return i.name.substr(0, 3);
      });
      roomArr = new Set(roomArr);
      roomArr = [...roomArr];
      return roomArr.sort((a, b) => {
        return a > b ? 1 : -1;
      });
    },
    // 取得指定病房目前居住的人
    selectRoomUsers() {
      let match = this.userItems.filter((i) => {
        return JSON.stringify(i.bed_name).includes(this.selectRoom.bed_name.split('-')[0]);
      });
      return match.sort((a, b) => {
        return a.bed_name > b.bed_name ? 1 : -1;
      });
    },
    sunday() {
      return dayjs(this.nowday).weekday(0).format("YYYY-MM-DD");
      // return dayjs(this.nowday).endOf("week").add(1, "day").format("YYYY-MM-DD");
    },
    saturday() {
      return dayjs(this.nowday).weekday(6).format("YYYY-MM-DD");
    },
  },

  methods: {
    //回傳指定user及日期有沒有資料
    matchData(userSnkey, day) {
      let match = this.selectRoomData.find((i) => {
        return i.userSnkey == userSnkey && i.date == day;
      });
      if (match) {
        return match;
      } else {
        return "";
      }
    },
    //設定選擇房間中資料的時間
    setRoomTime() {
      this.$nextTick(() => {
        this.selectRoomData.forEach((i) => {
          let space = `${i.date}_${i.userSnkey}`;
          this.$refs[space][0].innerHTML = i.time;
        });
        // console.log(this.$refs["2021-04-18_157"][0].innerHTML);
        // this.$refs["2021-04-18_157"][0].innerHTML = "123456";
      });
    },
    getAllData() {
      this.$api.options(
        "general/getAll/" + this.$store.state.databaseName + "/vieworder"
      ).then((rs) => {
        this.items = rs.map((i) => {
          let newItem = JSON.parse(i.datalist);
          newItem.snkey = i.snkey;
          return newItem;
        });
      });
    },
    //新增/修改預約內容
    addOrder(user, orderData, date) {
      let data = {
        user: user,
        orderData: orderData,
        date: dayjs(this.sunday).add(date, "day").format("YYYY-MM-DD"),
      };

      this.$refs.editProcess.$emit("fromParent", data);
    },
    //刪除預約內容
    del(delData) {
      this.$confirm("確認刪除?").then((res) => {
        delData.delman =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          snkey: delData.snkey,
          tablename: "vieworder",
          info: JSON.stringify(delData),
        };

        if (res) {
          this.$api.options(
            "general/delv3/" + this.$store.state.databaseName + "/vieworder",
            postData
          ).then((rs) => {
            if (rs.state == 1) {
              var pop = { msg: "已刪除", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.getAllData();
            }
          });
        }
      });
    },
    //回傳本周日期()
    returnDay(day, type = null) {
      if (type == "month") {
        return dayjs(this.sunday)
          .add(day - 1, "day")
          .format("MM-DD");
      } else {
        return dayjs(this.sunday)
          .add(day - 1, "day")
          .format("YYYY-MM-DD");
      }
    },
    //切換上下周
    changeday(day) {
      this.nowday = dayjs(this.nowday).add(day, "day").format("YYYY-MM-DD");
    },
    //取得各種資料
    getItems(url, dataKey) {
      this.$api.options(url).then((rs) => {
        this[dataKey + "Items"] = rs;
      });
    },
  },
};
</script>

<style scoped>
.theme--light.v-data-table thead tr th {
  font-size: 1rem;
  text-align: center;
}
.v-data-table td {
  text-align: center;
}
.v-data-table td:hover {
  background-color: #b3e5fc;
}
</style>