<template>
  <div class="ViewOrderSearch">
    <v-dialog v-model="dialog" width="80%">
      <template v-slot:activator="{ on }">
        <v-btn class="primary" v-on="on" @click="enterProcess"
          >指定日期預約狀況</v-btn
        >
      </template>

      <v-form ref="form">
        <v-card>
          <v-card-title
            class="
              d-flex
              justify-space-between
              primary
              lighten-3
              font-weight-bold
            "
          >
            <div>指定日期預約狀況</div>

            <div class="d-flex align-center">
              <v-text-field
                type="date"
                label="日期設定"
                v-model="selectDate"
                class="mr-5"
                hide-details
                :rules="emptyRules"
              ></v-text-field>

              <!-- <div class="mr-2">
                <v-btn
                  class="success"
                  @click="editProcess = !editProcess"
                  v-if="!editProcess"
                >
                  修改模式
                </v-btn>
                <v-btn class="primary" @click="editFinish()" v-else>
                  修改完成
                </v-btn>
              </div> -->

              <v-icon @click.stop="dialog = false">mdi-close</v-icon>
            </div>
          </v-card-title>

          <v-card-text class="mt-2">
            <v-simple-table class="form">
              <thead>
                <tr class="grey lighten-2">
                  <th>床號</th>
                  <th>姓名</th>
                  <th>日期</th>
                  <th>時間</th>
                  <th>預約單內容</th>
                  <th>記錄人員資料</th>
                </tr>
              </thead>

              <tbody class="text-no-wrap">
                <tr v-for="item in filterMatchDate" :key="item.snkey">
                  <td>
                    <v-icon class="error mr-2" @click.stop="del(item)"
                    v-if="!editProcess"
                      >mdi-close</v-icon
                    >
                    {{ item.bed_name }}
                  </td>
                  <td>{{ item.name }}</td>
                  <td style="min-width: 150px">
                    <v-text-field
                      type="date"
                      v-model="item.date"
                      v-if="editProcess"
                      solo
                      hide-details
                      class="ma-1"
                    ></v-text-field>
                    <span v-else>{{ item.date }}</span>
                  </td>
                  <td>
                    <v-text-field
                      type="time"
                      v-model="item.time"
                      v-if="editProcess"
                      solo
                      hide-details
                      class="ma-1"
                    ></v-text-field>
                    <span v-else>{{ item.time }}</span>
                  </td>
                  <td>
                    <!-- <v-text-field
                      v-model="item.content"
                      v-if="editProcess"
                      solo
                      hide-details
                      class="ma-1"
                    ></v-text-field>
                    <span v-else>{{ item.content }}</span> -->
                    {{ item.content }}
                  </td>
                  <td>
                    {{ item.create_man }}
                  </td>
                </tr>
              </tbody>
            </v-simple-table>
          </v-card-text>
          <v-divider></v-divider>

          <v-card-actions>
            <v-spacer></v-spacer>
            <!-- <v-btn class="primary" @click.stop="dialog = false">關閉</v-btn> -->
          </v-card-actions>

        </v-card>
      </v-form>
    </v-dialog>
  </div>
</template>

<script>
import dayjs from "dayjs";

export default {
  props: ["items", "userItems"],
  data() {
    return {
      dialog: false,
      emptyRules: [(v) => !!v || "不可空白"],
      list: {},
      selectDate: dayjs().format("YYYY-MM-DD"),
      editProcess: false,
    };
  },

  computed: {
    filterMatchDate() {
      return this.items
        .filter((i) => i.date == this.selectDate)
        .map((i) => {
          let match = this.userItems.find(
            (useri) => useri.snkey == i.userSnkey
          );
          return {
            ...i,
            name: match.name,
            bed_name: match.bed_name,
          };
        });
    },
  },

  // mounted() {
  //   //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
  //   this.$on("fromParent", function () {
  //     this.dialog = true;
  //     this.enterProcess();
  //   });
  // },

  methods: {
    //打開dialog進入程序
    enterProcess() {
      this.editProcess = false;
    },
    editFinish() {
      this.$confirm("確認完成修改?").then((res) => {
        if (res) {
          console.log(this.filterMatchDate);
          this.editProcess = !this.editProcess;
        }
      });
    },
    del(delData) {
      this.$confirm("確認刪除?").then((res) => {
        delData.delman =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          snkey: delData.snkey,
          tablename: "vieworder",
          info: JSON.stringify(delData),
        };

        if (res) {
          this.$api.options(
            "general/delv3/" +
              this.$store.state.databaseName +
              "/" +
              postData.tablename,
            postData
          ).then((rs) => {
            if (rs.state == 1) {
              var pop = { msg: "已刪除", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.$emit("getAllData");
            }
          });
        }
      });
    },
  },
};
</script>

<style scoped>
.v-data-table th {
  font-size: 1.2rem;
}
.v-data-table td {
  font-size: 1.1rem;
}
</style>